}

.alterra-logo {
  /*position: absolute;
  top: 10px;
  left: 25px;*/
  margin-left: 25px;
  width: 300px;
}

.slick-slide-heading {
  position: absolute;
  font-size: 74px;
  line-height: 76px;
  font-weight: bold;
  letter-spacing: 0px;
  margin-bottom: 22px;
  height: auto;
  top: 30%;
  width: 64vw;
  text-align: center;
  z-index: 200;
  color: white;
  padding: 0 18vw;
}

.slick-slide-description {
  position: absolute;
  top: calc(30% + 250px);
  width: 64vw;
  text-align: center;
  z-index: 200;
  color: white;
  font-size: 24pt;
  line-height: 36pt;
  font-weight: 500;
  padding: 0 18vw;
}

.slick-slide-image {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
}

.portfolio-page-slider {
  margin: 0 auto;
  width: 1100px;
  height: auto;
}

.portfolio-page-slider .slick-slide-image {
  object-fit: unset;
  width: 1100px;
  height: auto;
}
.slick-arrow.slick-next,
.slick-arrow.slick-prev {
  z-index: 201;
  background-color: rgba(255,255,255,.35);
  height: 70px;
  width: 70px;
  line-height: 70px;
  color: #fff;
  top: calc(50% - 35px);
}

.portfolio-page-slider .slick-arrow.slick-next, 
.portfolio-page-slider .slick-arrow.slick-prev,
.portfolio-page-slider .slick-prev:before, 
.portfolio-page-slider .slick-next:before {
  color: #0093E9;
}

.slick-arrow.slick-next {
   right: 0;

}

.slick-arrow.slick-prev {
  left: 0;
}

.slick-arrow.slick-next:before,
.slick-arrow.slick-prev:before {
  content: '\003C';
  font-family: unset;
  font-size: 35px;
  position: absolute;
  top: calc(50% - 17.5px);
  left: calc(50% - 17.5px);
}

.slick-arrow.slick-next:before {
  content: '\003E';
  left: calc(50% - 2.5px);
}

.slick-dots {
  bottom: 25px;
}

.slick-dots li button:before,
.slick-dots li.slick-active button:before {
  font-size: 14px;
  padding-top: 1.5px;
  color: white;
}

.slick-dots li button {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-radius: 50%;
}

.slick-dots li.slick-active button {
  border: 2px solid white;
}

