  .blog-block-item {
    flex-basis: 40%;
  }
}

@media (min-width: 415px) and (max-width: 768px) {
  .alterra-logo {
    width: 200px;
    margin-right: 15px;
  }
  .slick-slide-heading {
    top: 30%;
  }
  .about-section-grid,
  .techno-items-global-wrapper {
    flex-wrap: wrap;
  }
  .technoly-item-wrapper,
  .about-section-item {
    flex-basis: 40%;
    margin-top: 25px;
  }
  .meters-heading {
    padding-left: 25px;
  }
  .contact-form-block {
    padding: 0 25px;
  }
}

