  }
  .alterra-header>div {
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    background: #202020;
  }
  .slick-slider {
    margin-top: 67px;
  }
  .slick-slide-image {
    width: 100%;
    height: auto;
    object-fit: none;
  }
  .slick-slide-heading {
    top: 25px;
    font-size: 30px;
    line-height: 30px;
  }
  .slick-slide-description {
    top: 100px;
    font-size: 11pt;
    line-height: 14pt;
  }
  .alterra-header>div>span {
    padding-right: 25px;
    color: white;
  }
  .alterra-header>div>span.active+nav {
    display: block;
    opacity: 1;
    height: auto;
    transition: opacity 1s ease-out, height 1s ease-out;
  }
  .about-description,
  .portfolio-description {
    padding: 0 25px;
  }
  .alterra-meters-block>.ultrawrapper {
    padding: 0 25px;
  }
  .portfolio-item {
    flex-basis: 100%;
  }
  .blog-block-item {
    flex-basis: 100%;
  }
  .contact-form-block {
    padding: 25px;
  }
  .contact-form-block,
  .call-to-action-block {
    flex-wrap: wrap;
  }
  .full-form-element, 
  .contact-form-block_additional-text,
  .contacts-section_item {
    flex-basis: 100%;
  }
  .call-to-action-text {
    flex-basis: 100%;
    margin-bottom: 25px;
  }
  .slick-slider.portfolio-page-slider {
    margin-top: 0;
  }
  .portfolio-page-slider {
    width: 100vw;
  }
  .portfolio-page-slider .slick-slide-image {
    width: 100vw;
  }
  .slick-arrow.slick-next, .slick-arrow.slick-prev {
    top: 50%;
  }
  .portfolio-page-content-wrapper, 
  .article-page-content-wrapper {
    width: 100%;
  }
  .portfolio-page-heading_content, 
  .article-page-heading_content {
    padding: 0 25px;
  }
  .portfolio-page-text, .article-page-text {
    padding: 0 25px;
  }
  .article-page-block .article-main-img {
    width: 100vw;
  }
  .article-page-block .article-main-img, 
  .article-page-heading_top, 
  .article-page-content-wrapper {
    width: 100%;
  }
  .article-page-block img {
    width: 80vw;
  }
  .article-page-heading_top {
    padding: 15px 0;
  }
  .article-page-block ol>li, .article-page-block ul>li {
    transform: translate(0, 0);
  }
  .technologies-block-heading,
  .about-heading,
  .meters-heading,
  .portfolio-heading,
  .blog-block-heading {
    padding: 0 25px;
    font-size: 24px;
  }
  .blog-block-heading,
  .meters-wrapper {
    padding-top: 25px;
  }
}

@media (min-width: 320px) and (max-width: 376px) {
  .slick-slide-heading {
    font-size: 20px;
    line-height: 20px;
    top: 15px;
  }
  .slick-slide-description {
    top: 75px;
    font-size: 8.5pt;
    line-height: 10pt;
  }
  .slick-arrow.slick-next, 
  .slick-arrow.slick-prev {
    height: 40px;
    width: 40px;
    line-height: 40px;
  }

}